<template>
  <div class="page">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="微信提现"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content withdraw-wechat">

      <div class="wechat-name">
        <div class="title">微信绑定银行卡的姓名</div>
        <label>
          <input type="text" v-model="form.real_name" placeholder="输入真实姓名">
        </label>
      </div>

      <withdraw-options :options="wechatInfo.wechatOptions" :selected-index="selectedIndex" :on-select="onSelect"/>
      <withdraw-options :options="wechatInfo.wechatBigOptions" :selected-index="selectedBigIndex" :on-select="onBigSelect"/>

      <div class="tips-content">
        <span v-show="selected">消耗咖币 <span v-text="withdrawMoneyNumber" class="assertive fontSemibold"></span>，</span><span>当前剩余咖币 <span class="assertive fontSemibold">{{info.balance}}</span></span>
        <div class="tips">注：赠送筹码仅限每天首次成功提现</div>
      </div>

      <button :disabled="buttonDisabled" :class="{'button-active': !buttonDisabled}"  class="button button-withdraw" @click="submit()">
        提现
      </button>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .withdraw-wechat {
    margin-top: 50px;
    background: #FFF;
    padding: 25px 30px 25px 30px;

    .wechat-name {
      width: 200px;
      border-bottom: 1px solid #ddd;
      padding-bottom: 3px;

      .title {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;

        @include font-regular();
      }

      label {
        display: block;
        height: 33px;
        width: 100%;

        input {
          height: 33px;
          width: 100%;
          display: inline-block;
          line-height: normal;
          font-size: 24px;
          color: #3A3A3A;
          padding: 0;

          @include font-regular();

          &::-webkit-input-placeholder {
            font-size: 24px;
            color: #DDDDDD;
            line-height: 33px;
            height: 33px;

            @include font-regular();
          }
        }
      }
    }

    .tips-content {
      height: 41px;
      font-size: 13px;
      color: #484746;

      @include font-regular();

      .tips {
        margin-top: 5px;
        line-height: 18px;
      }

      .fontSemibold {
        @include font-semibold();
      }
    }

    .button-withdraw {
      width: 172px;
      height: 40px;
      min-height: 40px;
      background: #DDDDDD;
      border-radius: 2px;
      line-height: 40px;
      margin-top: 30px;
      font-size: 13px;
      color: #FFFFFF;

      @include font-semibold();
    }

    .button.disabled, .button[disabled] {
      opacity: 1;
    }

    .button-active {
      background: #ef502b;
    }
  }
</style>
<script>
  import withdrawOptions from './WithdrawOptions.vue'
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {
    popup_wechat_bind,
    popup_user_freeze,
    popup_bind_phone
  } from '../popup.js'
  import {mapState, mapActions, mapGetters} from 'vuex'

  export default {
    components: {
      withdrawOptions,
      AnimateHeader
    },

    data () {
      return {
        form: {
          real_name: '',
          price: ''
        },
        selected: true,
        selectedIndex: 0,
        selectedBigIndex: -1
      }
    },

    computed: {
      ...mapState('withdraw', [
        'info'
      ]),

      withdrawMoneyNumber () {
        if (this.selectedIndex !== -1) {
          return this.wechatInfo.realprices[this.selectedIndex]
        }
        if (this.selectedBigIndex !== -1) {
          return this.wechatInfo.realpricesLarge[this.selectedBigIndex]
        }
      },

      buttonDisabled () {
        if (this.form.real_name !== '') {
          return false
        }
        return true
      },

      ...mapGetters('withdraw', [
        'wechatInfo'
      ])
    },

    mounted () {
      this.fetch()
    },

    methods: {
      fetch () {
        this.getWithdrawList().then(() => {
          this.form.real_name = this.wechatInfo.real_name
        })
      },

      ...mapActions('withdraw', [
        'getWithdrawList',
        'submitWechat'
      ]),

      back () {
        $router.back('/withdraw/')
      },

      onSelect (index) {
        this.selected = true
        this.selectedIndex = index
        if (this.selectedIndex > -1) this.selectedBigIndex = -1
      },

      onBigSelect (index) {
        this.selected = true
        this.selectedBigIndex = index
        if (this.selectedBigIndex > -1) this.selectedIndex = -1
      },

      showBindPhonePopup () {
        let popup = popup = $popup.fromTemplate(
          popup_bind_phone.template,
          popup_bind_phone.options
        )

        popup.show().then(buttonIndex => {
          if (buttonIndex === 1) {
            $router.forward({
              path: '/user/bindMobile/0',
              query: { backroute: '/withdraw/weixin' }
            })
          }
        })
      },

      submit () {
        if (this.submitLock) return
        this.submitLock = true
        setTimeout(() => { this.submitLock = false }, 1000)

        this.selectedIndex > -1 && (this.form.price = this.wechatInfo.amount[this.selectedIndex])
        this.selectedBigIndex > -1 && (this.form.price = this.wechatInfo.amountLarge[this.selectedBigIndex])

        if (this.form.price > this.info.balance) {
          $toast.show('账户余额不足')
          return
        }

        if (!this.info.mobile_bind) {
          this.showBindPhonePopup()
          return
        }

        if (this.info.wechat.enabled === 0) { // 需要绑定微信
          let p = popup_wechat_bind
          let popup = $popup.fromTemplate(p.template, p.options)
          popup.show().then(btn => {
            if (btn === 0) { // 取消
            } else if (btn === 1) { // 前往微信绑定页面
              $router.forward({
                path: '/user/bindWeixin',
                query: {backroute: '/withdraw/weixin'}
              })
            }
          })
          return
        }

        this.submitWechat(this.form).then((res) => {
          if (res.status === 200) {
            if (res.data.err_code === 90000) {
              let popup = $popup.fromTemplate(
                popup_user_freeze.template(),
                popup_user_freeze.options
              )
              popup.show()
            } else {
              $router.forward({
                path: '/withdraw/result/weixin'
              })
            }
          }
        })
      }
    }
  }
</script>



